import React from 'react';

const Contact = () => {
    return (
        <div className="ui container portfolio-home">
        <div className="portfolio-intro ui container stackable grid">
            <div className="row">
                <div className="contact-desc">
                    <h2>Feel free to reach out!</h2>
                    <h3><i className="linkedin icon"></i><a className="contact-linkedin" href="https://www.linkedin.com/in/cynthia-deng/">
                        Connect with me on LinkedIn
                    </a></h3>
                    <h3><i className="envelope icon"></i>Send me an email at cynthiascdeng@gmail.com</h3>
                </div>      
            </div>
         </div>
         </div>
    );
};

export default Contact;